import navBar from '@/components/nav-bar.vue';
import dividLine from '@/components/line.vue';
import {
	changeName
} from '@/api/user'
export default {
	data() {
		return {
			name: '',
			isShow: false,
			phone: ''
		}
	},
	components: {
		navBar,
		dividLine
	},
	onLoad() {
		this._getUser()
	},
	methods: {
		back(){
			uni.navigateBack({
				delta:1
			})
		},
		_getUser() {
			try {
				const user = uni.getStorageSync('user');
				this.name = user.username;
				this.phone = user.phone
				console.log(user)
			} catch (e) {
				console.log('ERROR', e.message)
			}
		},
		clear() {
			this.name = '';
		},
		_getinput(e) {
			this.name = e.detail.value;
		},
		submit() {
			changeName({
				phone: this.phone,
				name: this.name
			}).then(res => {
				if (res.code == 200) {
					uni.showToast({
						title: res.msg
					})
					try {
						const user = uni.getStorageSync('user');
						user.username = this.name;
						uni.setStorageSync('user', user);
					} catch (e) {

					}
					setTimeout(() => {
						uni.reLaunch({
							url:'/pages/index/setting'
						}) 
					}, 500)

				}
			})
		}
	},
	watch: {
		name() {
			if (!this.name) {
				this.isShow = false
			} else {
				this.isShow = true
			}
		}
	}
}
